  }),
  metaInfo: {
    title: '7 suy nghĩ luôn có trong người “Thành Công” - Qùa Tặng Cuộc Sống',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_1575079368_7-suy-nghi-luon-co-trong-nguoi-8220thanh-cong8221.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: '7 suy nghĩ luôn có trong người “Thành Công”' },
      { name: 'Description', content: 'Niềm tin thứ 1: Mọi việc xảy ra đều có lý do Những người thành công đều có khả năng tập trung vào điểm tích cực trong hoàn cảnh của họ và nhận ra các kết quả tích cực mà chúng đem lại. Họ luôn nghĩ đến những khả năng tích cực...' },

