o đó. Người thành công trong xã hội không phải là người không thất bại. Họ cứ mạnh dạn thử sức, nếu nó không đem lại kết quả như mong muốn thì họ xem đó như là một bài học kinh nghiệm. Họ áp dụng những gì đã học được và thử sức ở việc khác. Họ thử sức ở những việc mới và tạo ra những kết quả mới."),_c('br'),_c('br'),_vm._v(" Có khi chúng ta học hỏi từ lỗi sai của mình, có khi chúng ta rút kinh nghiệm từ sai lầm của người khác. Tin vào sự thất bại chính là cách để đầu độc tâm trí. Vương mang cảm xúc tiêu cực sẽ ảnh hưởng xấu đến tâm lý, quá trình tư duy và trạng thái của ta. Một trong những điều khiến mọi người dễ chùn chân nhất chính là sợ thất bại."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Niềm tin thứ 3: Dù bất cứ chuyện gì xảy ra, hãy nhận trách nhiệm về mình"),_c('br'),_c('br'),_vm._v("  Những người thành đạt thường tin rằng bất cứ việc gì xảy ra, dù  tốt hay xấu, chính họ là người đã tạo ra nó. Nếu họ không tạo ra nó bằng những hành động cụ thể, có thể họ đã tạo ra nó bằng… suy nghĩ. Tạo ra những trải nghiệm trong cuộc sống – bằng hành động hoặc bằng suy nghĩ – và chúng ta có thể học hỏi từ tất cả trải nghiệm đó."),_c('br'),_c('br'),_vm._v(" Nếu bạn không tin chính bạn là người tạo ra thế giới của mình, cho dù đó là thành công hay thất bại, cuộc sống của bạn có vẻ như được an bày sẵn. Mọi việc cứ xảy ra với bạn. Bạn là khách thể chứ không phải là chủ thể. Tại sao chúng ta phải ở đây nếu như chúng ta chỉ là một sản phẩm được tạo nên bởi những quyền lực ngẫu nhiên từ bên ngoài?"),_c('br'),_c('br'),_vm._v(" Niềm tin thứ 4: Không nhất thiết phải hiểu hết mọi khía cạnh để có thể vận dụng điều gì đó "),_c('br'),_c('br'),_vm._v(" Chúng ta không cần phải hiểu hết mọi khía cạnh để có thể vận dụng điều gì đó. Ta cần biết cách sử dụng những gì cần thiết mà không để mình sa lầy vào việc tìm hiểu chi tiết. Nghiên cứu về những người có quyền lực, bạn sẽ nhận thấy họ có kiến thức để làm rất nhiều việc nhưng thường không tinh thông tất cả các chi tiết trong doanh nghiệp của họ."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Hãy chăm chăm vào bản chất của sự việc, tìm kiếm những gì ta cần và không quan tâm đến những thứ còn lại.  Và phải luôn ý thức được ta cần gì. Ta phải biết điều gì là cần thiết, điều gì không."),_c('br'),_c('br'),_vm._v(" Niềm tin thứ 5: Con người là vốn quý nhất "),_c('br'),_c('br'),_vm._v("  Những cá nhân xuất sắc, những người tạo ra kết quả xuất sắc, thường dành cho mọi người sự tôn trọng và cảm kích sâu sắc. Họ luôn có ý thức đồng đội, đoàn kết để cùng hướng đến những mục đích chung. Ở các nhà máy tại Nhật, công nhân và nhà quản lý ăn cùng với nhau trong căn-tin và tất cả đều đóng góp vào quá trình đánh giá kết quả. Kỳ tích có thể đạt được khi chúng ta tôn trọng mọi người thay vì cố gắng thao túng họ."),_c('br'),_c('br'),_vm._v(" Chúng ta phải luôn tỉnh táo, điều chỉnh lại hành vi của mình để chắc chắn rằng chúng ta đang tiến đến nơi mình muốn đến. Phải biết rằng khi chỉ có một người, dù cho người ấy có xuất sắc đến đâu, cũng sẽ rất khó khăn để làm tốt bằng một đội gồm những người tài năng làm việc hiệu quả cùng nhau."),_c('br'),_c('br'),_vm._v(" Niềm tin thứ 6: Làm việc cũng là vui chơi"),_c('br'),_c('br'),_vm._v("  Bạn có biết người nào đạt được thành công to lớn khi làm những việc mà họ ghét? Một trong những chìa khóa dẫn đến thành công là có một cuộc “hôn phối” tốt đẹp giữa những gì bạn làm và những gì bạn thích. Dường như đó là những gì mà những người thành công đang làm."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Đối với một số người, c